.navigation {
  display: block;
  width: 100%;
  height: 47px;
  box-shadow: 0 5px 5px rgba(16, 16, 15, 0.5);
  background-color: #db3534;
}

.navigation__container {
  display: block;
  width: 1168px;
  margin: 0 auto;
  padding-top: 5px;
}

.navigation__menu-button {
  display: block;
  position: relative;
  z-index: 4;
  float: left;
  width: 35px;
  height: 26px;
  background: #db3534 url("../assets/menu-button.svg") 100% no-repeat;
  background-size: cover;
  outline: none;
  border: none;
  margin-top: 6px;
}

.navigation__menu-button:hover {
  transition: 0.5s;
  transform: translate(0, 5px);
}

.navigation__menu-button:focus {
  transition: 0.9s;
  transform: translate(0, 0);
}

.navigation__menu {
  display: none;
  opacity: 0;
  position: absolute;
  z-index: 2;
}

.navigation__triangle-svg-top {
  position: absolute;
  top: 45px;
  left: 10px;
}

.navigation__items-box {
  background-color: black;
  opacity: 0.851;
  padding: 25px 26px 3px 23px;
  position: relative;
  top: 45px;
}

.navigation__item {
  list-style-type: none;
  padding-bottom: 23px;
}

.navigation__item a {
  text-decoration: none;
  text-transform: uppercase;
  color: #fefefe;
  font-family: "Helvetica Neue-Light", sans-serif;
  font-size: 14px;
}

.navigation__item a:hover {
  color: #ff826f;
  transition: 0.7s;
}

.navigation__order-items {
  display: block;
  width: 160px;
  float: left;
  margin-left: 596px;
  margin-right: 6px;
}

.navigation__basket-box {
  float: left;
}

.navigation__basket-info {
  display: inline-block;
  width: 120px;
  color: #ffffff;
  font-family: "Helvetica Neue-Light", sans-serif;
  font-size: 12px;
  padding-left: 8px;
}

.navigation__basket-info:first-child {
  width: auto;
  float: right;
  padding-top: 5px;
}

.navigation__basket-info:first-child:hover {
  transform: rotate(25deg);
  transition: 2s;
}

.navigation__language {
  display: inline-block;
  float: left;
  margin-top: -16px;
  /*    input-animation    */
}

.navigation__language input {
  display: none;
}

.navigation__language p {
  display: inline-block;
  width: 33px;
  height: 33px;
  position: relative;
  color: #ff3f3e;
  text-align: center;
  font-family: "Helvetica Neue-Light", sans-serif;
  font-size: 18px;
  background-color: rgba(0, 0, 0, 0.302);
  box-shadow: 0 1px 1px rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.5);
  padding-top: 7px;
  margin-right: 3px;
}

.navigation__language input[type="radio"]:checked + p {
  color: #ffffff;
}

.navigation__search {
  display: inline-block;
  width: 272px;
  height: 33px;
  border: none;
  background-color: rgba(0, 0, 0, 0.302);
  box-shadow: 0 1px 1px rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.5);
  margin-top: 2px;
  margin-left: 23px;
}

.navigation__search-field {
  display: inline-block;
  position: relative;
  border: none;
  outline: none;
  width: 230px;
  color: #ff3f3e;
  font-family: "Helvetica Neue-Light", sans-serif;
  font-size: 24px;
  background-color: transparent;
  padding: 4px 0 2px 8px;
  top: -6px;
}

.navigation__search-field::placeholder {
  color: #ff3f3e;
}

.navigation__search-button {
  display: inline-block;
  width: 33px;
  height: 33px;
  outline: none;
  border: none;
  background: url("../assets/search.png") 50% no-repeat rgba(0, 0, 0, 0);
  padding-right: 10px;
  margin-right: 5px;
}

.navigation__search-button:hover {
  transform: scale(0.8);
  transition: 0.8s;
}

.show {
  display: block;
  opacity: 1;
}

