*,
*:before,
*:after {
  box-sizing: border-box;
}
@font-face {
  font-family: "Helvetica Neue-Light";
  src: url("../fonts/Helvetica Neue/HelveticaNeueCyr-Light.otf");
  font-style: normal;
  font-weight: 300;
}
@font-face {
  font-family: "Helvetica Neue-Medium";
  src: url("../fonts/Helvetica Neue/HelveticaNeueCyr-Medium.otf");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Helvetica Neue - Medium Italic";
  src: url("../fonts/Helvetica Neue/HelveticaNeueCyr-MediumItalic.otf");
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: "Helvetica Neue-Bold";
  src: url("../fonts/Helvetica Neue/HelveticaNeueCyr-Bold.otf");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Bebas Neue Bold";
  src: url("../fonts/Bebas Neue/BebasNeue Bold.otf");
  font-style: normal;
  font-weight: 400;
}
body {
  margin: 0;
}